@import '../../assets/styles/style.scss';

.scoreBoard {
  min-height: 350px;
  width: calc(100% - 60px);
  margin: 0 30px 30px;
  background: #00000030;
  color: white;
  border-radius: 10px;
  max-width: 360px;
  
  &__wrapper {
    width: 100%;
    height: 100%;
    padding: 30px 20px;
  }

  &__header {
    font-size: 1.5em;
    margin-bottom: 30px;
    letter-spacing: .5px;
    font-weight: bold;
  }

  &__info {
    &__moves {
      padding: 10px;
    }
    &__options {
      display: flex;
      justify-content: center;
      padding: 15px 0;
      flex-wrap: wrap;
      margin: 0px 0 5px;
      color: $darkWhite;
      min-height: 200px;

      @include rwd(360) {
        justify-content: flex-start;
      }
    }
  
  }

  @include phone {
    & {
      max-width: 360px;
      margin: 0 10px;
    }
  }
  @include desktop {
    margin-left: 50px;
  }
}



.scoreBoard__info__header {
  font-size: 1.3em;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.85);
}

.dots {
  font-size: 1.4em;
}
.dot {
  display: inline-block;
}
.dot:nth-child(1) {
  animation: jump 1.5s linear infinite;
}
.dot:nth-child(2) {
  animation: jump 1.5s 0.3s linear infinite;
}
.dot:nth-child(3) {
  animation: jump 1.5s 0.6s linear infinite;
}
@keyframes jump {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0);
  }
}
